const queryByName = (name) => `
[out:json][timeout:25];
area["name"="${name}"]->.searchArea;
(
  way["highway"~"primary|secondary|tertiary|unclassified"](area.searchArea);
);
out body;
>;
out skel qt;
`;

// loc in latlng
// radius in meter
const queryByRadius = (loc, radius) => `
[out:json][timeout:25];
(
  way["highway"~"primary|secondary|tertiary|unclassified"](around:${radius}, ${loc});
);
out body;
>;
out skel qt;
`;

const queryByBoundingBox = (min_lat, min_lon, max_lat, max_lon) => `
[out:json][timeout:25];
(
  way["highway"~"primary|secondary|tertiary|unclassified"](${min_lat},${min_lon},${max_lat},${max_lon});
);
out body;
>;
out skel qt;
`;

